import { useState, useEffect } from "react";
import { GridList } from "./components/GridList";
/* import Button from "@material-ui/core/Button";
import DescriptionRoundedIcon from "@material-ui/icons/DescriptionRounded"; */
import { makeStyles } from "@material-ui/core/styles";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
/* import FormHelperText from "@material-ui/core/FormHelperText"; */
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";

import "./App.css";

const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));

function App() {
  const [url, setUrl] = useState("");
  const [data, setData] = useState([]);
  const [search, setSearch] = useState("");
  const [dateBefore, setDateBefore] = useState("");
  const [dateAfter, setDateAfter] = useState("");
  const classes = useStyles();

  const handleChangeBefore = (e) => {
    setDateBefore(e.target.value);
    if (url.length <= 52) {
      setUrl(`${url}&brewed_before=01-${e.target.value}`);
      setDateAfter("");
      setSearch("");
    } else {
      setSearch("");
      setUrl(`https://api.punkapi.com/v2/beers?&brewed_before=01-${e.target.value}`);
      setDateAfter("");
    }
  };

  const handleChangeAfter = (e) => {
    setDateAfter(e.target.value);
    if (url.length <= 52) {
      setUrl(`${url}&brewed_after=12-${e.target.value}`);
      setDateBefore("");
      setSearch("");
    } else {
      setSearch("");
      setUrl(`https://api.punkapi.com/v2/beers?brewed_after=12-${e.target.value}`);
      setDateBefore("");
    }
  };

  useEffect(() => {
    fetch(url)
      .then((response) => response.json())
      .then((data) => {
        setData(data);
        console.log(data);
      });
  }, [url]);

  function searchFn() {
    if (search.length !== 0) {
      setUrl(`https://api.punkapi.com/v2/beers?beer_name=${search.replaceAll(" ", "_")}`);
      setDateBefore("");
      setDateAfter("");
    } else {
      alert("no empty search field please");
    }
  }

  return (
    <div className="App">
      <div className="search">
        <input
          value={search}
          type="text"
          placeholder="search for a beer"
          name="search"
          id="s"
          onChange={(e) => {
            setSearch(e.target.value);
          }}
          onKeyPress={(e) => {
            if (e.key === "Enter") {
              searchFn();
            }
          }}
        />

        <div className="button" id="b" onClick={() => searchFn()}>
          SEARCH
        </div>
        {url.length !== 0 && (
          <div className="buttonWrapper">
            <FormControl className={classes.formControl}>
              <InputLabel id="demo-simple-select-label">
                only show beers before...
              </InputLabel>
              <Select
                value={dateBefore}
                style={{ minWidth: "200px" }}
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                onChange={handleChangeBefore}>
                <MenuItem value={2010}>2010</MenuItem>
                <MenuItem value={2011}>2011</MenuItem>
                <MenuItem value={2012}>2012</MenuItem>
                <MenuItem value={2013}>2013</MenuItem>
                <MenuItem value={2014}>2014</MenuItem>
                <MenuItem value={2015}>2015</MenuItem>
                <MenuItem value={2016}>2016</MenuItem>
              </Select>
            </FormControl>
            <FormControl className={classes.formControl}>
              <InputLabel id="demo-simple-select-label2">
                only show beers after...
              </InputLabel>
              <Select
                value={dateAfter}
                style={{ minWidth: "200px" }}
                labelId="demo-simple-select-label2"
                id="demo-simple-select2"
                onChange={handleChangeAfter}>
                <MenuItem value={2010}>2010</MenuItem>
                <MenuItem value={2011}>2011</MenuItem>
                <MenuItem value={2012}>2012</MenuItem>
                <MenuItem value={2013}>2013</MenuItem>
                <MenuItem value={2014}>2014</MenuItem>
                <MenuItem value={2015}>2015</MenuItem>
              </Select>
            </FormControl>
          </div>
        )}
      </div>
      {data.length !== 0 ? <GridList data={data} /> : "No such beer"}
    </div>
  );
}

export default App;
