import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";

import CardContent from "@material-ui/core/CardContent";
/* import CardMedia from "@material-ui/core/CardMedia"; */
import Button from "@material-ui/core/Button";

import Typography from "@material-ui/core/Typography";
/* import LocalDrinkIcon from "@material-ui/icons/LocalDrink"; */
import DescriptionRoundedIcon from "@material-ui/icons/DescriptionRounded";
import TransitionModal from "./Modal";

const useStyles = makeStyles({
  root: {
    maxWidth: 345,
    minWidth: 300,
    margin: "1rem",
  },
  media: {
    height: 140,
  },
});

export function GridList(props) {
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const [id, setId] = useState(0);
  let propsData = props.data;

  const buttonClicked = (page) => {
    window.open(page, "_blank");
  };

  const handleOpen = () => {
    setOpen(true);
  };

  return (
    <div
      className="cardWrapper"
      style={{
        width: "100%",
        margin: "auto",
        display: "flex",
        /* flexDirection: "column", */
        flexWrap: "wrap",
        justifyContent: "center",
        /* alignItems: "center", */
        /* alignContent: "flex-start", */
      }}>
      {props.data &&
        props.data.map((data, index) => {
          return (
            <Card
              key={index}
              id={index}
              className={`${classes.root} Card`}
              style={{
                display: "flex",
                flexDirection: "column",
                justifyContent: "space-between",
                alignItems: "center",
              }}>
              <CardActionArea
                id={index}
                key={index}
                onClick={() => buttonClicked(`${data.image_url}`)}
                /* style={{
                  height: "90%",
                }} */
              >
                <img
                  src={
                    data.image_url
                      ? `${data.image_url}`
                      : "https://s3.amazonaws.com/FringeBucket/image_placeholder.png"
                  }
                  alt="cover art"
                  style={{ width: "auto", margin: "1rem auto", maxHeight: "20rem" }}
                />
              </CardActionArea>
              <CardContent
              /* style={{
                  display: "flex",
                  flexDirection: "column",
                  justifyContent: "space-between",
                  alignItems: "center",
                }} */
              >
                <Typography gutterBottom variant="h5" component="h2">
                  {data.name}
                </Typography>
                <Typography gutterBottom variant="h6" component="h4">
                  {data.tagline}
                </Typography>
                <Typography
                  variant="body2"
                  color="textSecondary"
                  component="p"
                  style={{ margin: "1rem auto" }}>
                  {data.description ? data.description : "No description"}
                </Typography>

                <Typography variant="body1" color="textSecondary" component="p">
                  {data.food_pairing
                    ? `Goes well with: ${data.food_pairing[0]} `
                    : "You can drink it with anything"}
                </Typography>
                <Typography
                  variant="body1"
                  color="textSecondary"
                  component="p"
                  style={{ margin: "1rem auto" }}>
                  {data.first_brewed
                    ? `First brewed: ${data.first_brewed} `
                    : "No data about when this was brewed first"}
                </Typography>
              </CardContent>

              <Button
                id={index}
                className={classes.root}
                variant="outlined"
                startIcon={<DescriptionRoundedIcon />}
                onClick={(e) => {
                  setId(e.currentTarget.id);
                  console.log(id);
                  handleOpen();
                }}>
                Details
              </Button>
            </Card>
          );
        })}

      {props.data && (
        <TransitionModal open={open} setOpen={setOpen} data={propsData} id={id} />
      )}
    </div>
  );
}
