import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";
import { makeStyles } from "@material-ui/core/styles";
/* import { useState } from "react"; */
/* import Button from "@material-ui/core/Button";
import TheatersIcon from "@material-ui/icons/Theaters"; */

const useStyles = makeStyles((theme) => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    width: "90%",
    margin: "auto",
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: "2px solid #000",
    borderRadius: "7px",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
    textAlign: "center",
  },
  Backdrop: {
    backgroundColor: "rgba(60, 77, 49, .5)",
  },
}));

export default function TransitionModal(props) {
  const classes = useStyles();
  /*  const [open, setOpen] = useState(props.open); */

  /*   const handleOpen = () => {
    setOpen(true);
  }; */

  const handleClose = () => {
    props.setOpen(false);
  };

  return (
    <div>
      {props.data[props.id] && (
        <Modal
          disableEnforceFocus
          aria-labelledby="transition-modal-title"
          aria-describedby="transition-modal-description"
          className={classes.modal}
          open={props.open}
          onClose={handleClose}
          closeAfterTransition
          BackdropComponent={Backdrop}
          BackdropProps={{
            timeout: 500,
            className: classes.Backdrop,
          }}>
          <Fade in={props.open}>
            <div className={classes.paper}>
              <h2 id="transition-modal-title">
                {props.data[props.id].name ? `${props.data[props.id].name}` : "no name"}
              </h2>
              <h3 style={{ color: "#008000" }}>Description:</h3>
              <h4 id="transition-modal-description">
                {props.data[props.id].description
                  ? `${props.data[props.id].description}`
                  : "no description"}
              </h4>
              <h4 style={{ color: "#008000" }}>Goes well with:</h4>
              <h5>
                {props.data[props.id].food_pairing
                  ? props.data[props.id].food_pairing.map((food, index) => {
                      return <p key={index}>- {food}</p>;
                    })
                  : "no food pairing suggestion"}
              </h5>
              <h4 style={{ color: "#008000" }}>Malts used:</h4>
              <h5>
                {props.data[props.id].ingredients.malt
                  ? props.data[props.id].ingredients.malt.map((malt, index) => {
                      return <span key={index}>{malt.name}, </span>;
                    })
                  : "no malt ingredients"}
              </h5>
              <h4 style={{ color: "#008000" }}>Hops used:</h4>
              <h5>
                {props.data[props.id].ingredients.hops
                  ? props.data[props.id].ingredients.hops.map((hops, index) => {
                      return <span key={index}>{hops.name}, </span>;
                    })
                  : "no malt ingredients"}
              </h5>
              <span style={{ color: "#008000" }}>Tips: </span>
              <span style={{ fontSize: "0.9em" }}>
                {props.data[props.id].brewers_tips
                  ? props.data[props.id].brewers_tips
                  : "no brewers tips"}
              </span>
            </div>
          </Fade>
        </Modal>
      )}
    </div>
  );
}
